import React from 'react';


const AnnounsmentItem = ({announcement}) => {
    let showForm;
    showForm = () =>{
        let obj = document.getElementById('edit');
            obj.style.display = "block";
    }

    return (


        <li key={announcement.date.toString()}>
            <h4>{announcement.title}</h4>
            <p>{announcement.description}</p>
            <span>{announcement.date}</span> <br/>
            <button className='edit' onClick={showForm}>Edit</button>
            <button className='del'>Delete</button>

        </li>


    );
}
export default AnnounsmentItem;