import React from 'react';


export class Add extends React.Component {

    render() {
        return (
            <div className="Add">

                <form>
                    <label>Title</label><input type='text'/><br/>
                    <label>Description</label><textarea/><br/>

                    <button type='submit'>Add</button>
                </form>
            </div>
        );
    }
}
